import widgetEmpty from '@/widget/empty'
import layoutTabbar from '@/layout/tabbar'
import layoutNavbar from '@/layout/navbar'
import layoutNavbarColor from '@/layout/navbar-color'
import layoutNavbarShadow from '@/layout/navbar-shadow'
import widgetScrollBox from '@/widget/scroll-box'
import widgetBackgroundColor from '@/widget/background-color'
import navigationEffectBox from '@/navigation/effect-box'
import list from './list'
import plus from '@/img/plus.svg'

export default {
  components: {
    list,
    widgetEmpty,
    layoutTabbar,
    layoutNavbar,
    layoutNavbarColor,
    layoutNavbarShadow,
    widgetScrollBox,
    widgetBackgroundColor,
    navigationEffectBox
  },
  data () {
    return {
      plus
    }
  },
  computed: {
    list () {
      return _(this.$storage.wish.map)
      .map(({ name, title, content }, key) => {
        const time = +key.slice(0, -4)
        return {
          name,
          time,
          title,
          content
        }
      })
      .sort((m, n) => {
        return n.time - m.time
      })
      .value()
    },
    color () {
      let color = ''
      if (this.list.length) {
        color = '#bdc5ca'
      }
      return color
    },
  },
  methods: {
    async navbarTap ({ type }) {
      if (type === 'right') {
        this.$push('wish-add')
      }
    }
  }
}



